.btb-vue-table-info {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}
.btb-vue-table-info .info_table {
  width: 100%;
  border-collapse: collapse;
}
.btb-vue-table-info .info_table .tr_th {
  background: #d3d3d3;
  width: 50%;
}
.btb-vue-table-info .info_table + .table_info {
  margin-top: 0.5rem;
}
.btb-vue-table-info .info_empty {
  min-height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}